.floatRight2 {
  width: 40%;
  float:right;

}
.endButton {
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  border-top-right-radius: 7px;
  border-bottom-right-radius: 7px;
  
  -ms-flex-align: start;
  
      align-items: flex-start;
  text-align: center;
  cursor: default;
  color: buttontext;
  background-color: buttonface;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 2px 6px 3px;
  border-width: 2px;
  border-style: outset;
  border-color: buttonface;
  -webkit-border-image: initial;
       -o-border-image: initial;
          border-image: initial;
  margin-top:-2px;
  height:24px;
  
}
.beginningText {
  border-top-left-radius: 7px;
  border-bottom-left-radius: 7px;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;

  background-color: white;
  -webkit-user-select: text;
     -moz-user-select: text;
      -ms-user-select: text;
          user-select: text;
  cursor: auto;
  padding: 1px;
  border-width: 2px;
  border-style: inset;
  border-color: initial;
  -webkit-border-image: initial;
       -o-border-image: initial;
          border-image: initial;
  height: 18px;
  
}

.signup-container {
  color:#fff;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
      -ms-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
}

.table-div {
  margin-left:10%;
}


.home-second-nav {
 margin:auto;
 text-align: center;
}

.home-divider {
  height:40px;
}

.home-divider20 {
  height:20px;
}

.home-parallax {
  color: #fff;
  /* The image used */
  background-image: url("http://paultrujillo.net/dev/ui-html/images/bg-login.png");

  /* Set a specific height */
  height: 250px;
  
  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
}

.number-steps {
    background: #fff;
    border: 3px solid #2e91d3;
    border-radius: 50%;
    padding: 10px 16px;
    font-weight: bold;
    color: #2e91d3;
}

.steps-line {
  border-color: #2e91d3;
  height: 80px;
  border-right: 3px dashed #2e91d3;
  position: relative;
  right: 50%;
  top: 12px;
  margin-bottom:20px;
}


